@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
}

input[type='text']:focus { box-shadow: none; }

.scrollsettings::-webkit-scrollbar-track
{
    border-radius: 15px;
	-webkit-box-shadow: inset 0 0 1px rgba(0,0,0,0.1);
	background-color: #F5F5F5;
}

.scrollsettings::-webkit-scrollbar
{
	width: 7px;
	background-color: #F5F5F5;
}

.scrollsettings::-webkit-scrollbar-thumb
{
	background-color: #1C64F2;	
    border-radius: 15px;
	background-image: -webkit-linear-gradient(45deg,
	rgba(255, 255, 255, .2) 25%,
	transparent 25%,
	transparent 50%,
	rgba(255, 255, 255, .2) 50%,
	rgba(255, 255, 255, .2) 75%,
	transparent 75%,
	transparent)
}
.rdt_TableHeadRow{
	background-color: #F3F4F6 !important;
	border-top: 2px solid #212529 !important;
}
